@import "https://fonts.googleapis.com/css?family=Roboto+Mono";

body {
  font-family: "Roboto Mono", sans-serif;
  text-align: center;
  background: linear-gradient(to bottom right, #542437 0%, #cdb380 100%) 100%
    no-repeat;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

#calculator {
  box-shadow: 0 0 4rem 0 rgba(0, 0, 0, 0.5);
}

h1 {
  text-transform: uppercase;
  text-align: center;
  margin-top: 0;
  margin-bottom: 35px;
  font-size: 40px;
}

#display {
  background-color: #343838;
  border-bottom: 3px solid #5e6f8c;
  padding: 30px 10px;
  text-align: right;
}

p {
  text-align: right;
  font-size: 22px;
  margin: 0;
  color: #cdb380;
  margin-right: 45px;
}

#actions {
  display: inline-flex;
  flex: 1;
  flex-wrap: wrap;
  border: none;
}

button {
  width: 25%;
  font-size: 18px;
  font-family: "Ubuntu", sans-serif;
  padding: 30px 8px;
  outline: none;
  border-radius: 0;
  background: #343838;
  color: #cdb380;
  border: none;
  cursor: pointer;
  transition: color 300ms ease-in-out;
}

button:hover {
  color: #5e6f8c;
}

#actions :first-child {
  width: 50%;
}

#actions :nth-child(18) {
  width: 50%;
}

@media (max-width: 567px) {
  body {
    margin: 0 10px;
  }
}
